import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)

const store = new Vuex.Store({

    state: {
        isLoading: false,
        selectedMonth: {
            year: null,
            month: null,
        },
        searchHistories: [],
        videos: [],
        channels: [],
    },

    getters: {
        count: state => {
            return state.count;
        },

        isLoading: state => {
            return state.isLoading;
        },

        selectedMonth: state => {
            return state.selectedMonth;
        },

        //TODO: 選択中の月の動画のみをフィルターして返却する
        videosInSelectedMonth: state => {
            return state.count;
        },

        //TODO: 選択中の月内の検索履歴をフィルターして返却
        searchHistoriesInSelectedMonth: state => {
            return state.count;
        },

        //TODO: ↑のチャンネル情報のみをフィルターして返却
        channelsInSelectedMonth: state => {
            return state.count;
        },

        channels: state => {
            return state.channels;
        },

    },

    mutations: {

        //ローディング
        startLoading(state) {
            state.isLoading = true;
        },
        endLoading(state) {
            state.isLoading = false;
        },

        //月の変更
        changeMonth(state, newMonth) {
            state.selectedMonth = newMonth;

            store.dispatch('searchExecute');
        },

        //検索履歴の追加
        addSearchHistory(state, newSearchHistory) {
            state.searchHistories.push(newSearchHistory);
        },

        //チャンネル情報を追加
        addChannel(state, Channel) {
            //同一チャンネルIDのデータが既に存在しているかをチェック
            let exist = state.channels.find((ch) => {
                return ch.ChannelId == Channel.ChannelId;
            });

            if (exist === void 0) {
                state.channels.push(Channel);
            }
        },

        //動画配列をvideosに追加する
        addVideos(state, Videos) {
            let newVideos = [];

            console.log(Videos);

            if(Videos==null) return;

            Videos.forEach(function (video) {
                //同一動画IDのデータがすでに存在しているものに含まれるかをチェック
                let filtered = state.videos.filter(existingVideo => {
                    return existingVideo.VideoId == video.VideoId;
                });

                if (filtered.length < 1) {
                    //完全新規の動画
                    //投稿日時のstringをパースしてDateTimeへ
                    video.PublishedAt = new Date(Date.parse(video.PublishedAt));

                    newVideos.push(video);
                }
            });

            //マージ
            state.videos = state.videos.concat(newVideos);

            //投稿日時順にソート
            state.videos = state.videos.sort(function (a, b) {
                if (a.PublishedAt < b.PublishedAt) return -1;
                if (a.PublishedAt > b.PublishedAt) return 1;
                return 0;
            });

        },

    },

    actions: {

        //動画の検索実行
        searchExecute({ commit, state }) {

            //検索履歴をチェックして既に検索済みであればなにもしない
            let searchHistory = state.searchHistories.find(function (sh) {
                return sh.year == state.selectedMonth.year
                    && sh.month == state.selectedMonth.month;
            });

            if (searchHistory) {
                //検索済み
                return;
            }

            let _url = "https://h5zom8lt7e.execute-api.ap-northeast-1.amazonaws.com/release";

            let _param = {
                year: state.selectedMonth.year,
                month: state.selectedMonth.month
            };
            //ローディング開始
            commit('startLoading');

            axios
                .post(_url, _param)
                .then(function (response) {
                    //返ってきた動画をストアに追加
                    commit('addVideos', response.data.body.videos);
                    //commit('addChannel', response.data.body.channel);
                })
                .finally(function () {
                    //ローディングを終了
                    commit('endLoading');

                    //検索履歴を登録
                    commit('addSearchHistory', _param);
                });
        },


    },

})


export default store