<template>
  <div class="video-item">
    <a v-bind:href="videoURL">
      <div class="mb-1">
        <img
          v-bind:src="thumnailURL"
          data-toggle="tooltip"
          data-placement="bottom"
          v-bind:title="this.video.Title"
        />
      </div>
    </a>
  </div>
</template>

<script>
export default {
  name: "VideoItem",
  props: ["video"],
  data() {
    return {};
  },
  computed: {
    videoURL: function() {
      return "https://youtu.be/" + this.video.VideoId;
    },
    thumnailURL: function() {
      return "https://i.ytimg.com/vi/" + this.video.VideoId + "/default.jpg";
    }
  },
  methods: {}
};
</script>

