<template>
  <div>
    <div class="wrapper" align="center">
      <vc-calendar
        class="vc-calendar"
        title-position="center"
        :max-date="new Date()"
        @update:from-page="pageChange"
        is-expanded
      >
        <template slot="header-title" slot-scope="props">{{props.yearLabel}}年{{props.monthLabel}}</template>
        <template slot="day-content" slot-scope="props">
          <div class="cell-header" v-bind:class="computeClass(props.day)">{{props.day.day}}</div>
          <div class="cell-content text-center" v-bind:class="computeClass(props.day)">
            <template>
              <div
                class="cell-content-line"
                v-for="video in getVideoInDate(props.day.date)"
                v-bind:key="video.PublishedAt.toISOString()"
              >
                <span>
                  <VideoItem class="mb-1" v-bind:video="video"></VideoItem>
                </span>
              </div>
            </template>
          </div>
        </template>
      </vc-calendar>
    </div>
  </div>
</template>

<script>
import VideoItem from "./VideoItem.vue";

export default {
  name: "MainCalendar",
  components: {
    VideoItem: VideoItem,
  },
  data() {
    return {
      //videos: [],
      searchHistories: [],
    };
  },

  mounted: function () {
    //検索実行
  },

  computed: {
    selectedMonth: {
      get() {
        return this.$store.state.selectedMonth;
      },
    },

    videos: {
      get() {
        return this.$store.state.videos;
      },
    },
  },
  methods: {
    //DateTime型をYYYYMMDDの文字列に置換
    dateToYYYYMMDD: function (date) {
      let y = date.getFullYear();
      let m = ("00" + (date.getMonth() + 1)).slice(-2);
      let d = ("00" + date.getDate()).slice(-2);
      let result = y + "" + m + "" + d;
      return result;
    },

    dateToSecond: function (date) {
      return Date(date);
    },

    //dateの日付に含まれるvideoをarrayにして返す
    getVideoInDate: function (date) {
      //当日0時と翌日0時
      let sDt = date;
      let eDt = new Date(sDt.getTime());
      eDt.setDate(eDt.getDate() + 1);

      const videos = this.videos.filter((video) => {
        return video.PublishedAt >= sDt && video.PublishedAt < eDt;
      });

      return videos;
    },

    //v-calendarの月変更時
    pageChange: function (obj) {
      this.$store.commit("changeMonth", obj);
    },

    //cell-header/cell-contentに付加するクラス
    computeClass: function (props) {
      let retStr = "";

      if (props.day == 1 || props.weekday == 1) {
        //1日/日曜日は最左列
        retStr += "on-left";
      }

      if (props.dayFromEnd <= 7) {
        //後ろから7日間は最下段
        retStr += " on-bottom";
      }

      return retStr;
    },
  },
};
</script>

